.game-body {
    padding-top: 50px;
    background: url("../img/general/mk1.png");
    background-size: cover;
}

.game-body-wrap {
    display: grid;
    width: 90%;
    margin: auto;
    grid-template-columns: repeat(auto-fit, 300px);
    @media screen and(max-width: 320px) {
        width: auto;
    }
    @media screen and (min-width: 321px) and(max-width: 500px) {
        width: 75%;
    }
}

.game-body-col {
    width: 90%;
    margin: 10px;
    padding: 15px 10px;
    background-color: $sandy-color;
    opacity: .8;
}

.game-body-center {
    display: flex;
    justify-content: space-between;
    padding: 5px;
    margin-top: 80px;
}


.game-body-square {
    width: 80px;
    height: 100px;
    background-color: $grey-color;
    opacity: 8;
}

.game-body-button {
    display: block;
    margin: 50px auto 0;
    background-color: $button-color;
    padding: 20px 100px;
    border-radius: 20px;
    outline: none;
}

