.header-nav-wrap {
    background-color: $black-color;
    height: 8vh;
    @media screen and(max-width: 900px) {
        height: auto;
        text-align: center;
    }
}

.header-nav-list {
    display: flex;
    justify-content: space-between;
    list-style: none;
    width: 300px;
    margin-left: 60%;
    @media screen and(max-width: 900px) {
        flex-direction: column;
        margin: auto;
    }
}


.header-nav-link {
    position: relative;
    border: 1px solid transparent;
    padding: 5px;
    transition: all .4s ease;
    color: $white-color;
    text-decoration: none;
    vertical-align: center;
    line-height: 3;
    border-radius: 10px;

    &:hover {
        box-shadow: 5px 5px 20px $white-color, -5px -5px 20px $white-color;
        background-color: $black-color;
        border-color: $white-color;
        color: $white-color;

        &:before {
            content: "";
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: $white-color;
        }

        &:after {
            content: "";
            position: absolute;
            bottom: -50%;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: $white-color;
        }
    }
}

